.bg__menuMin{height:100%;width:100%;position:fixed;top:0;z-index:2;background:rgba(155,155,155,.05);box-shadow:0 8px 32px 0 var(--var-shadow-nav);backdrop-filter:blur(0px);-webkit-backdrop-filter:blur(16px) saturate(180%);visibility:hidden;background-color:rgba(155,155,155,0);transition:all .5s ease-in-out}.menu__container{position:fixed;bottom:-450px;width:100%;background-color:var(--var-white);padding:3rem 0rem;z-index:2;border-radius:16px 16px 0px 0px;transition:bottom .5s ease-in-out}.menu__container .menu__list{color:var(--var-black)}.menu__container .menu__list a{display:flex;padding:1rem 3rem;align-items:center;align-content:center}.menu__container .menu__list a:hover::before{content:"•";position:absolute;left:1.5rem;font-size:1.3rem}.menu__container .menu__list .button-att{margin-left:1rem}.menu__container .selected{background-color:var(--var-black);color:var(--var-white)}.menu__container .selected::before{content:"•";position:absolute;left:1.5rem;font-size:1.3rem}.menu__container .selected span,.menu__container .selected div{background-color:var(--var-black);color:var(--var-white)}.showMenu{bottom:46px;transition:bottom .5s ease-in-out,background-color .3s}.showMenuBg{visibility:visible;backdrop-filter:blur(4px);background-color:rgba(155,155,155,.5);transition:all .5s ease-in-out}