.container__full {
    margin-top: 10rem;
    position: relative;
    .container__discount {
        display: flex;
        flex-direction: column;
        padding: 2rem 0rem;

        figure {
            display: flex;
            justify-content: flex-end;
            .Circle {
                height: 9rem;
                width: 9rem;
                background-color: var(--var-secondary-light-gray);
                border-radius: 50%;
                //display: inline-block;
                position: relative;
                margin-top: 3rem;
                &::after {
                    content: '';
                    position: absolute;
                    background-color: var(--white);
                    border-radius: 50%;
                    width: 3rem;
                    height: 3rem;
                    bottom: 0px;
                    left: -14px;
                    //border-radius: 0px 10px 0px 30px;
                    box-shadow: 0px 0px 10px var(--white);
                }
            }
        }
    }
}

@media (min-width: 768px) {
    .container__full {
        .container__discount {
        }
    }
}
